.title {
  font-size: large;
  position: relative;
  display: inline-block;
  font-family: Helvetica;
  font-weight: bold;
  font-size: 24px; }
  .title--cardContainer {
    margin: -30px 0 70px 0;
    padding: 24px 32px;
    text-align: center;
    width: 100%; }

@media screen and (min-width: 1200px) {
  .title::after, .title::before {
    content: '';
    background: black;
    display: block;
    width: auto;
    height: 2px;
    position: absolute;
    top: 50%;
    z-index: 0; }
  .title::before {
    left: 100%;
    right: -400px;
    -webkit-transform: translateX(15px);
        -ms-transform: translateX(15px);
            transform: translateX(15px); }
  .title::after {
    left: -400px;
    right: 100%;
    -webkit-transform: translateX(-15px);
        -ms-transform: translateX(-15px);
            transform: translateX(-15px); } }



